@font-face {
  font-family: "MonoAlphabet";
  src: url("../fonts/mono-alphabet-font/MonoalphabetRegular-OMvP.ttf");
}

@font-face {
  font-family: "PressStart2P";
  src: url("../fonts/press-start-2p/PressStart2P.ttf");
}

@font-face {
  font-family: "Magneto";
  src: url("../fonts/MAGNETOB.ttf");
}

html {
  background: #000000;
  color: #fefefe;
  width: calc(100vw - 30px);
  margin-left: 15px;
  margin-right: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 0px;
  font-size: 100%;
  font-family: "PressStart2P", monospace;
}

body {
  margin: 0px;
  padding: 0px;
  width: 100%;
}

.spacereviews {
  font-family: "Magneto", "PressStart2P", 'Brush Script MT', cursive;
  font-size: 11vw;
  z-index: -1;
  height: 100%;
  width: 100%;
  text-align: center;
  align-self: stretch;
  vertical-align: middle;
}

p {
  font-size: 1em;
}

.current {
  color: chartreuse;
}

nav {
  position: absolute;
  right: 15px;
  display: flex;
  align-content: space-evenly;
}

a {
  color: inherit;
  text-decoration: none;
}

h1 {
  font-size: 3em;
}

h2 {
  font-size: 2em;
  text-decoration: underline;
}

h3 {
  font-size: 2em;
}

h4  {
  font-size: 1em;
  text-decoration: underline;
}

h5  {
  font-size: 1em;
  font-variant: small-caps;
}

h6 {
  font-size: 1em;
  font-weight: bold;
}